import React from 'react'
import DayListItem from "./DayListItem"
const DayList = ({days, propsDay, setDay} = this.props) => {
    return (
        <div>
            <ul>
            {days.map((day, key) => 
           < DayListItem key={key}
           name={day.name} 
           spots={day.spots} 
           selected={day.name === propsDay}
           setDay={setDay}
           />
            )},
            </ul>
        </div>
    )
}

export default DayList